import React, { Component } from 'react';
import axios from 'axios';


export default class Home extends Component {
    static async getInitialProps() {
        axios
    }


    constructor() {
        super();
        this.state = {
            dogs: ''
        }
    }


    render() {
        return (
            <div>
                Welcome Bruh!
                <img src={this.state.dogs}/>
            </div>
        )
    }
}



